import React, { Component } from "react";
import styles from "./mandatoryChallengeButton.module.css";

class MandatoryChallengeButton extends Component {
    render() {
        const toggleState = this.props.mandatoryChallenge;
        let classes = this.getClasses(toggleState);
        return (
            <div
                onClick={() => this.props.onToggleMandatoryChallenge(!toggleState)}
                className={classes}
            >
                <span className="display-4">30'</span>
            </div>
        );
    }

    getClasses(toggleState) {
        let classes = "white mb-1 ";
        classes += toggleState ? styles.btn : styles.btn_secondary;
        return classes;
    }
}

export default MandatoryChallengeButton;
