import React, { Component } from "react";
import styles from "./sickButton.module.css";

class SickButton extends Component {
    render() {
        const toggleState = this.props.sick;
        let classes = this.getClasses(toggleState);
        return (
            <div
                onClick={() => this.props.onToggleSick(!toggleState)}
                className={classes}
            >
                <span className="display-4">Sick leave</span>
            </div>
        );
    }

    getClasses(toggleState) {
        // console.log("this.props.sick ", toggleState);
        let classes = "white mb-1 ";
        classes += toggleState ? styles.btn : styles.btn_secondary;
        // console.log("classes: ", classes);
        return classes;
    }
}

export default SickButton;
