import React, { Component } from "react";
import styles from "./extraChallengeButton.module.css";

class ExtraChallengeButton extends Component {
    render() {
        const toggleState = this.props.extraChallenge;
        let classes = this.getClasses(toggleState);
        return (
            <div
                onClick={() => this.props.onToggleExtraChallenge(!toggleState)}
                className={classes}
            >
                <span className="display-4">Xtra</span>
            </div>
        );
    }

    getClasses(toggleState) {
        let classes = "white mb-1 ";
        classes += toggleState ? styles.btn : styles.btn_secondary;
        return classes;
    }
}

export default ExtraChallengeButton;
