import React, { Component } from "react";
import Day from "./day";
import ScoreLabel from "./scoreBadge";
import Calendar from "./calendar";
import axios from "axios";

class User extends Component {
  constructor() {
    super();
    this.state = {
      user: {
        name: "",
        days: []
      }
    };
    const defaultDay = this.GetToday();
    this.state.user.days.push(defaultDay);
    this.state.dateToDisplay = defaultDay;
  }

  componentDidMount() {
    if (!this.state.user._id) {
      const userName = this.props.userName;
      axios
        .get(process.env.REACT_APP_API_URL + "/user/name/" + userName)
        .then(response => {
          // handle success
          // // console.log("responseData: ", response.data);
          let user = response.data;

          this.setState({ user: user }, () => {
            const defaultDay = this.GetToday();
            this.setState({ dateToDisplay: defaultDay });
          });
          // // console.log("state object from componentDidMount: ", this.state.user);
          // // console.log("componentDidMount day object: ", this.state.dateToDisplay);
        })
        .catch(err => {
          // user does not exist and must be created.
          let newUser = { ...this.state.user };
          newUser.name = userName;
          axios
            .post(process.env.REACT_APP_API_URL + "/user/add/", newUser)
            .then(res => {
              newUser._id = res.data._id;
              newUser.days[0]._id = res.data.day_id;
              this.setState({ user: newUser });
            })
            .catch(err => {
              // // console.log("error when creating new user: ", err)
            });
          // // console.log("error when fetching user: ", err);
        });
    }
  }
  // state = {};

  CountFreePassUsed() {
    if (!this.state.user || !this.state.user.days) {
      return 0;
    }
    const days = [...this.state.user.days];
    const daysWithFreePass = days.filter(day => day.freePass === true);
    return daysWithFreePass.length;
  }

  CountSickDays() {
    if (!this.state.user || !this.state.user.days) {
      return 0;
    }
    const days = [...this.state.user.days];
    const sickDays = days.filter(day => day.sick === true);
    return sickDays.length;
  }

  countMaxNumberOfRedStrokesUntilYesterday() {
    if (!this.state.user || !this.state.user.days) {
      return 0;
    }
    let NumberOfRedStrokes = 0;
    let startDate = new Date(process.env.REACT_APP_START_DATE);
    let selectedDate = new Date(this.state.dateToDisplay.date);
    let millisecondsSinceBeginning = selectedDate - startDate;
    if (millisecondsSinceBeginning > 0) {
      NumberOfRedStrokes = (millisecondsSinceBeginning / (1000 * 60 * 60 * 24)) * 2;
    }
    console.log(NumberOfRedStrokes);
    return NumberOfRedStrokes;
  }


  CountRedStrokesUntilYesterday() {
    if (!this.state.user || !this.state.user.days) {
      return 0;
    }

    const days = [...this.state.user.days];
    let NumberOfRedStrokesUntilYesterday = this.countMaxNumberOfRedStrokesUntilYesterday();
    for (let day of days) {
      if (day.date < this.state.dateToDisplay.date) {
        if (day.sick || day.freePass) {
          NumberOfRedStrokesUntilYesterday -= 2;
        }
        if (day.mandatoryChallenge) {
          NumberOfRedStrokesUntilYesterday -= 1;
        }
        if (day.extraChallenge) {
          NumberOfRedStrokesUntilYesterday -= 1;
        }
        if (day.recalledChallenge) {
          NumberOfRedStrokesUntilYesterday -= 1;
        }
      }
    }
    return NumberOfRedStrokesUntilYesterday;
  }

  CalculateTotalScore() {
    if (!this.state.user || !this.state.user.days) {
      return 0;
    }
    const days = [...this.state.user.days];
    const reducer = (present, next) => present + next.dailyScore;
    const totalScore = days.reduce(reducer, 0);
    // // console.log("TotalScore: ", totalScore);
    return totalScore;
  }

  HandleDailyScoreUpdate = newScore => {
    // // console.log("newDailyScore: ", newScore);
  };

  HandleDayUpdate = updatedDay => {
    // console.log("HandleDayUpdate: ", updatedDay);
    this.setState({ dateToDisplay: updatedDay });

    const existingDay = this.state.user.days.some(
      day => day.date === updatedDay.date
    );
    // console.log("existing day: ", existingDay);
    if (!existingDay) {
      // new day
      // // console.log("!existingDay && !updatedDay._id: ", !existingDay, updatedDay._id, (!existingDay || !updatedDay._id));
      const user = { ...this.state.user };

      axios
        .post(process.env.REACT_APP_API_URL + "/user/" + user._id + "/day/add", updatedDay)
        .then(res => {
          updatedDay._id = res.data._id;
          const days = [...this.state.user.days, updatedDay];
          user.days = days;
          this.setState({ user }, () => {
            this.CalculateTotalScore();
          });
        });
    } else {
      // existing day
      const user = { ...this.state.user };

      axios
        .post(
          process.env.REACT_APP_API_URL + "/user/update/" +
          user._id +
          "/day/" +
          updatedDay._id,
          updatedDay
        )
        .then(res => {
          const days = [...this.state.user.days];
          const index = this.state.user.days.findIndex(
            day => day.date === updatedDay.date
          );
          // // console.log("index of existing day", index);
          days[index] = updatedDay;

          user.days = days;
          this.setState({ user }, () => {
            this.CalculateTotalScore();
          });
        });
    }
  };

  HandleNavigateTo = date => {
    // // console.log("Date to navigate to: ", date);
    const dayToDisplay = this.GetDay(date);
    this.setState({ dateToDisplay: dayToDisplay });
  };

  HandleCalendarClassnames = tile => {
    if (tile.view === "month") {
      const day = this.GetDay(tile.date);
      if (day.freePass) {
        return "freePassTile";
      }
      if (day.sick) {
        return "sickTile";
      }
      if (day.recalledChallenge) {
        return "recalledChallengeTile";
      }

      if (day.mandatoryChallenge && day.extraChallenge) {
        return "regularTile";
      }

      return "redDayTile";
    }

    return null;
  };

  GetToday() {
    const today = new Date();
    const endDate = new Date(process.env.REACT_APP_END_DATE);
    if (endDate < today) {
      return this.GetDay(endDate);
    }
    return this.GetDay(today);
  }

  GetDay(date) {
    const dateString = this.GetDateString(date);
    let day = [];
    // console.log("GetDay(date): ", date, this.state, this.state.user, this.state.user.days, (!this.state && !this.state.user && !this.state.user.days));
    // console.log("this.state: ", this.state, this.state != null);
    // console.log("this.state.user: ", this.state.user, this.state.user != null);
    // console.log("this.state.user.days: ", this.state.user.days, this.state.user.days != null);
    // console.log("should look for a match in array: ", this.state != null && this.state.user != null && this.state.user.days != null);
    if (
      this.state != null &&
      this.state.user != null &&
      this.state.user.days != null
    ) {
      day = this.state.user.days.filter(day => day.date === dateString);
    }

    if (day.length === 0) {
      return {
        date: dateString,
        dailyScore: 0,
        freePass: false,
        mandatoryChallenge: false,
        extraChallenge: false,
        recalledChallenge: false
      };
    }

    return { ...day[0] };
  }

  GetDateString(shortDate) {
    const date = new Date(shortDate);
    const dateString = new Date(
      date.getTime() - date.getTimezoneOffset() * 60000
    )
      .toISOString()
      .split("T")[0];
    return dateString;
  }

  render() {
    console.log("state at render: ", this.state);

    return (
      <span>
        <a href="/">
          <button type="button" className="btn btn-primary" style={{ width: '75%' }}>
            <span className="display-5">Home</span>
          </button>
        </a>
        <h2>{this.props.userName}</h2>
        <Day
          OnDailyScoreUpdate={this.HandleDailyScoreUpdate}
          OnDayUpdate={this.HandleDayUpdate}
          day={this.state.dateToDisplay}
          redStrokesExist={this.CountRedStrokesUntilYesterday() > 0}
        />
        <ScoreLabel
          scoreLabel={"Total score "}
          score={this.CalculateTotalScore()}
        />
        <ScoreLabel
          scoreLabel={"Free pass used "}
          score={this.CountFreePassUsed()}
        />
        <ScoreLabel
          scoreLabel={"Sick days "}
          score={this.CountSickDays()}
        />
        <ScoreLabel
          scoreLabel={"Red strokes "}
          score={this.CountRedStrokesUntilYesterday()}
        />
        <Calendar
          onNavigateTo={this.HandleNavigateTo}
          days={
            !this.state.user || !this.state.user.days
              ? []
              : this.state.user.days
          }
          startDate={process.env.REACT_APP_START_DATE}
          endDate={process.env.REACT_APP_END_DATE}
        />
      </span>
    );
  }
}

export default User;
