import React, { Component } from "react";
import styles from "./recalledChallengeButton.module.css";

class RecalledChallengeButton extends Component {
    render() {
        const toggleState = this.props.recalledChallenge;
        let classes = this.getClasses(toggleState);
        return (
            <div
                onClick={() => this.props.onToggleRecalledChallenge(!toggleState)}
                className={classes}
            >
                <span className="display-4">60'</span>
            </div>
        );
    }

    getClasses(toggleState) {
        let classes = "white mb-1 ";
        classes += toggleState ? styles.btn : styles.btn_secondary;
        return classes;
    }
}

export default RecalledChallengeButton;
