import React, { Component } from "react";
import FreePassButton from "./freePassButton";
import SickButton from "./sickButton";
import ExtraChallengeButton from "./extraChallengeButton";
import MandatoryChallengeButton from "./mandatoryChallengeButton";
import RecalledChallengeButton from "./recalledChallengeButton";
import ScoreLabel from "./scoreBadge";

class Day extends Component {
  HandleToggleFreePass = newPropertyState => {
    console.log("new Free Pass State: ", newPropertyState);
    const newState = { ...this.props.day };
    newState.freePass = newPropertyState;

    if (newPropertyState) {
      newState.mandatoryChallenge = false;
      newState.sick = false;
      newState.extraChallenge = false;
      newState.recalledChallenge = false;
    }

    this.UpdateState(newState);
  };

  HandleToggleMandatoryChallenge = newPropertyState => {
    console.log("new MandatoryChallenge State: ", newPropertyState);

    const newState = { ...this.props.day };
    newState.mandatoryChallenge = newPropertyState;

    if (newPropertyState) {
      newState.sick = false;
      newState.freePass = false;
    } else {
      newState.recalledChallenge = false;
    }

    this.UpdateState(newState);
  };

  HandleToggleSick = newPropertyState => {
    console.log("new Sick State: ", newPropertyState);

    const newState = { ...this.props.day };
    newState.sick = newPropertyState;

    if (newPropertyState) {
      newState.mandatoryChallenge = false;
      newState.freePass = false;
      newState.extraChallenge = false;
      newState.recalledChallenge = false;
    }

    this.UpdateState(newState);
  };

  HandleToggleExtraChallenge = newPropertyState => {
    console.log("new ExtraChallenge State: ", newPropertyState);

    const newState = { ...this.props.day };
    newState.extraChallenge = newPropertyState;

    if (newPropertyState) {
      newState.sick = false;
      newState.freePass = false;
    } else {
      newState.recalledChallenge = false;
    }
    this.UpdateState(newState);
  };

  HandleToggleRecalledChallenge = newPropertyState => {
    console.log("new RecalledChallenge State: ", newPropertyState);

    const newState = { ...this.props.day };

    if (!this.props.day.mandatoryChallenge || !this.props.day.extraChallenge || !this.props.redStrokesExist) {
      newPropertyState = false;
    }
    newState.recalledChallenge = newPropertyState;

    if (newPropertyState) {
      newState.sick = false;
      newState.freePass = false;
    }
    this.UpdateState(newState);
  };

  UpdateState(newState) {
    const newScore = this.ComputedDailyScore(newState);
    newState.dailyScore = newScore;
    this.props.OnDayUpdate(newState);
  }

  ComputedDailyScore(newState) {
    let newScore = 0;

    if (newState.sick) {
      return 2;
    }

    if (newState.freePass) {
      return 2;
    }

    if (newState.mandatoryChallenge) {
      newScore += 1;
    }
    if (newState.extraChallenge) {
      newScore += 1;
    }
    if (newState.recalledChallenge) {
      newScore += 1;
    }

    return newScore;
  }

  render() {
    return (
      <span>
        <div className="row">
          <div className="col d-flex justify-content-center">
            <div className="card">
              <div className="card-body">
                <div className="row mb-3">
                  <div className="col">
                    <span className="display-4">{this.props.day.date}</span>
                  </div>
                </div>
                <div className="row mb-3">
                  <div className="col-sm ">
                    <MandatoryChallengeButton
                      onToggleMandatoryChallenge={
                        this.HandleToggleMandatoryChallenge
                      }
                      mandatoryChallenge={this.props.day.mandatoryChallenge}
                    />
                  </div>
                  <div className="col-sm">
                    <ExtraChallengeButton
                      onToggleExtraChallenge={this.HandleToggleExtraChallenge}
                      extraChallenge={this.props.day.extraChallenge}
                    />
                  </div>
                  <div className="col-sm">
                    <RecalledChallengeButton
                      onToggleRecalledChallenge={
                        this.HandleToggleRecalledChallenge
                      }
                      recalledChallenge={this.props.day.recalledChallenge}
                    />
                  </div>
                </div>
                <div className="row mb-3">
                  <div className="col-sm">
                    <SickButton
                      onToggleSick={this.HandleToggleSick}
                      sick={this.props.day.sick}
                    />
                  </div>
                </div>
                <div className="row mb-3">
                  <div className="col-sm">
                    <FreePassButton
                      onToggleFreePass={this.HandleToggleFreePass}
                      freePass={this.props.day.freePass}
                    />
                  </div>
                </div>
              </div>
              <div className="row mb-3">
                <div className="col">
                  <ScoreLabel
                    scoreLabel={"Daily score"}
                    score={this.props.day.dailyScore}
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </span>
    );
  }
}

export default Day;
