import React, { Component } from "react";
import axios from "axios";
import { withRouter } from 'react-router-dom';

class Users extends Component {
  state = {
    users: [],
    inputValue: ''
  };

  componentDidMount() {
    axios
      .get(process.env.REACT_APP_API_URL + '/user')
      .then(res => {
        const users = res.data;
        let existingUsers = [];
        console.log(users);
        users.map(user => existingUsers.push({ name: user.name, totalScore: this.CalculateTotalScore(user), redStrokes: this.CountRedStrokesUntilYesterday(user) }));
        existingUsers.sort((a, b) => (a.redStrokes > b.redStrokes) ? 1 : -1);
        this.setState({ users: existingUsers }, () => {
          console.log("state: ", this.state);
        });
      })
      .catch(err => {
        console.log("error while fetching users. ", err);
      });
  }

  CalculateTotalScore(user) {
    if (user == null || user.days == null) {
      return 0;
    }
    const days = [...user.days];
    const reducer = (present, next) => present + next.dailyScore;
    const totalScore = days.reduce(reducer, 0);
    // // console.log("TotalScore: ", totalScore);
    return totalScore;
  }

  countMaxNumberOfRedStrokesUntilYesterday() {

    let NumberOfRedStrokes = 0;
    let startDate = new Date(process.env.REACT_APP_START_DATE);
    let endDate = new Date(process.env.REACT_APP_END_DATE);
    let selectedDate = new Date(this.GetTodayAsString());
    if (endDate < selectedDate) {
      selectedDate = endDate;
    }
    let millisecondsSinceBeginning = selectedDate - startDate;
    if (millisecondsSinceBeginning > 0) {
      NumberOfRedStrokes = (millisecondsSinceBeginning / (1000 * 60 * 60 * 24)) * 2;
    }
    console.log("MaxNumberOfRedStrokes: ", NumberOfRedStrokes);
    return NumberOfRedStrokes;
  }


  CountRedStrokesUntilYesterday(user) {
    if (user == null || user.days == null) {
      return 0;
    }
    const todayAsString = this.GetTodayAsString();
    const days = [...user.days];
    let NumberOfRedStrokesUntilYesterday = this.countMaxNumberOfRedStrokesUntilYesterday();
    for (let day of days) {
      if (day.date < todayAsString) {
        if (day.sick || day.freePass) {
          NumberOfRedStrokesUntilYesterday -= 2;
        }
        if (day.mandatoryChallenge) {
          NumberOfRedStrokesUntilYesterday -= 1;
        }
        if (day.extraChallenge) {
          NumberOfRedStrokesUntilYesterday -= 1;
        }
        if (day.recalledChallenge) {
          NumberOfRedStrokesUntilYesterday -= 1;
        }
      }
    }
    return NumberOfRedStrokesUntilYesterday;
  }

  GetTodayAsString() {
    const date = new Date();
    const dateString = new Date(
      date.getTime() - date.getTimezoneOffset() * 60000
    )
      .toISOString()
      .split("T")[0];
    return dateString;
  }

  GetPlaceIcon(arrayIndex) {

    const arrayLength = this.state.users.length;

    if (arrayIndex === 0) {
      return "btn btn-primary p-3 ranking-image firstPlace";
    }
    if (arrayLength - arrayIndex === 1) {
      return "btn btn-primary p-3 ranking-image lastPlace";
    }
    return "btn btn-primary p-3 ranking-image inbetween";
  }


  handleChange = event => {
    this.setState({ inputValue: event.target.value });
  }

  handleSubmit = event => {
    console.log("new user: ", this.state.inputValue);
    event.preventDefault();
    this.props.history.push('/' + this.state.inputValue);
  }

  handleUserClicked = (userName, event) => {
    event.preventDefault();
    console.log("handleUserClicked: ", userName);
    this.props.history.push('/' + userName);
  }

  render() {
    return (
      <div>
        {this.state.users.map((user, index) => (
          <div className="row mt-2" key={user.name}>
            <div className="col" >
              <button type="button" onClick={(e) => this.handleUserClicked(user.name, e)} className={this.GetPlaceIcon(index)} style={{ width: '75%' }}>
                <span className="display-5">
                  {user.name} <span className="badge badge-light pull-right">{user.redStrokes}</span>
                </span>
              </button>
            </div>
          </div>
        ))}
        <form onSubmit={this.handleSubmit}>
          <div className="row mt-5">
            <div className="col">
              <input type="text" value={this.state.value} onChange={this.handleChange} style={{ width: '75%' }} />
            </div>
          </div>
          <div className="row mt-2">
            <div className="col">
              <input type="submit" className="btn btn-primary p-3" value="add new user" style={{ width: '75%' }}></input>
            </div>
          </div>
        </form>
      </div>
    );
  }
}

export default withRouter(Users);
