import React, { Component } from "react";
import styles from "./freePassButton.module.css";

class FreePassButton extends Component {
  render() {
    const toggleState = this.props.freePass;
    let classes = this.getClasses(toggleState);
    return (
      <div
        onClick={() => this.props.onToggleFreePass(!toggleState)}
        className={classes}
      >
        <span className="display-4">Free Pass</span>
      </div>
    );
  }

  getClasses(toggleState) {
    // console.log("this.props.freePass ", toggleState);
    let classes = "white mb-1 ";
    classes += toggleState ? styles.btn : styles.btn_secondary;
    // console.log("classes: ", classes);
    return classes;
  }
}

export default FreePassButton;
