import React, { Component } from "react";
import styles from "./calendar.module.css";
import dateFns from "date-fns";

class Calendar extends Component {
    state = {
        currentMonth: new Date(),
        selectedDate: new Date()
    };

    renderHeader() {
        const dateFormat = "MMMM YYYY";

        return (
            <div
                className={[styles.header, styles.row, styles["flex-middle"]].join(" ")}
            >
                <div className={[styles.col, styles["col-start"]].join(" ")}>
                    <div className={styles.icon} onClick={this.prevMonth}>
                        chevron_left
          </div>
                </div>
                <div className={[styles.col, styles["col-center"]].join(" ")}>
                    <span>{dateFns.format(this.state.currentMonth, dateFormat)}</span>
                </div>
                <div className={[styles.col, styles["col-end"]].join(" ")}>
                    <div className={styles.icon} onClick={this.nextMonth}>
                        chevron_right
          </div>
                </div>
            </div>
        );
    }

    renderDays() {
        const dateFormat = "dddd";
        const days = [];

        let startDate = dateFns.startOfISOWeek(this.state.currentMonth);

        for (let i = 0; i < 7; i++) {
            days.push(
                <div className={[styles.col, styles["col-center"]].join(" ")} key={i}>
                    {dateFns.format(dateFns.addDays(startDate, i), dateFormat)}
                </div>
            );
        }

        return <div className={[styles.days, styles.row].join(" ")}>{days}</div>;
    }

    getCellClasses(day, monthStart, selectedDate) {
        let classes = [styles.col, styles.cell].join(" ");

        const classFromScoreType = this.getClassFromScoreType(day);

        if (!dateFns.isSameMonth(day, monthStart) || dateFns.isAfter(day, new Date()) || dateFns.isBefore(day, dateFns.format(this.props.startDate)) || dateFns.isAfter(day, dateFns.format(this.props.endDate))) {
            return [classes, styles.disabled].join(" ");
        }

        if (dateFns.isSameDay(day, selectedDate)) {
            classes = [classes, styles.selected].join(" ");
            if (classFromScoreType == null) {
                return [classes, styles.calendar_empty].join(" ")
            }
        }

        return [classes, this.getClassFromScoreType(day)].join(" ");
    }

    getClassFromScoreType(day) {
        const formattedDate = this.GetDateString(day);

        // console.log("formattedDate: ", formattedDate);

        const filteredDaysFromProp = this.props.days.filter(dayItem => dayItem.date === formattedDate);

        if (filteredDaysFromProp.length === 1) {
            const dayFromProp = filteredDaysFromProp[0];
            if (dayFromProp.freePass) {
                return styles.calender_freePass;
            }

            if (dayFromProp.sick) {
                return styles.calender_sick;
            }

            if (dayFromProp.recalledChallenge) {
                return styles.calender_recalledChallenge;
            }

            if (dayFromProp.mandatoryChallenge && dayFromProp.extraChallenge) {
                return styles.calender_mandatoryXtraChallenge;
            }

            if (dayFromProp.mandatoryChallenge || dayFromProp.extraChallenge) {
                return styles.calender_failedChallenge;
            }
        }
        return null;
    }

    GetDateString(shortDate) {
        const date = new Date(shortDate);
        const dateString = new Date(
            date.getTime() - date.getTimezoneOffset() * 60000
        )
            .toISOString()
            .split("T")[0];
        return dateString;
    }

    renderCells() {
        const { currentMonth, selectedDate } = this.state;
        const monthStart = dateFns.startOfMonth(currentMonth);
        const monthEnd = dateFns.endOfMonth(monthStart);
        const startDate = dateFns.startOfISOWeek(monthStart);
        const endDate = dateFns.endOfISOWeek(monthEnd);

        const dateFormat = "D";
        const rows = [];

        let days = [];
        let day = startDate;
        let formattedDate = "";

        while (day <= endDate) {
            for (let i = 0; i < 7; i++) {
                formattedDate = dateFns.format(day, dateFormat);
                const cloneDay = day;
                days.push(
                    <div
                        className={this.getCellClasses(day, monthStart, selectedDate)}
                        key={day}
                        onClick={() => this.onDateClick(dateFns.parse(cloneDay))}
                    >
                        <span className={styles.number}>{formattedDate}</span>
                        <span className={styles.bg}>{formattedDate}</span>
                    </div>
                );
                day = dateFns.addDays(day, 1);
            }
            rows.push(
                <div className={styles.row} key={day}>
                    {days}
                </div>
            );
            days = [];
        }

        return <div className={styles.body}>{rows}</div>;
    }

    onDateClick = day => {
        this.setState({
            selectedDate: day
        });
        this.props.onNavigateTo(day);
    };

    nextMonth = () => {
        this.setState({
            currentMonth: dateFns.addMonths(this.state.currentMonth, 1)
        });
    };

    prevMonth = () => {
        this.setState({
            currentMonth: dateFns.subMonths(this.state.currentMonth, 1)
        });
    };

    render() {
        return (
            <div className={styles.calendar}>
                {this.renderHeader()}
                {this.renderDays()}
                {this.renderCells()}
            </div>
        );
    }
}

export default Calendar;
